import React, { useState } from 'react'
import { connect } from 'react-redux'
import { addContact } from 'redux/operations'

 function Form({ onSubmit }) {
  const [name, setName] = useState('')
  const [number, setNumber] = useState('')
  const handleNameChange = (e) => setName(e.currentTarget.value)
  const handleNumberChange = (e) => setNumber(e.currentTarget.value)
  const handleSubmit = (e) => {
    e.preventDefault()
    onSubmit({ name, number })
    setName('')
    setNumber('')
  }
  return (
    <form type="submit" onSubmit={handleSubmit}>
      <label>
        Name
        <input
          value={name}
          type="text"
          name="name"
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
          required
          onChange={handleNameChange}
        />
      </label>
      <label>
        Number
        <input
          value={number}
          type="tel"
          name="number"
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
          required
          onChange={handleNumberChange}
        />
      </label>
      <button type="submit">Add contact</button>
    </form>
  )
}
const mapDispatchToProrps = (dispatch) => ({
  onSubmit: (obj) => dispatch(addContact(obj)),
})
export default connect(null, mapDispatchToProrps)(Form)